$mainColor: #f38181;
$mainColor2: #95e1d3;

$mainFont: "Roboto", sans-serif;
$handWritten: "Kaushan", serif;
$titleFont: "Montserrat", sans-serif;

body {
	font-family: $mainFont;
	font-size: 16px;
	position: relative;
	overflow-x: hidden; }


h1,h2,h3,h4,h5,h6,ul,body,p {
	margin: 0; }

ul {
	padding: 0;
	list-style-type: none; }

a {
	text-decoration: none; }


.container,
.flex_container {
	width: 1200px;
	margin: 0 auto; }

.flex_container {
	display: flex; }


.devider {
	width: 40px;
	height: 2px;
	display: block;
	margin: 20px auto; }

.devider--white {
	@extend .devider;
	background-color: #fff; }

.devider--red {
	@extend .devider;
	background-color: $mainColor; }

.section {
	padding: 100px 0; }

.section_title {
	text-align: center;
	line-height: 1;
	width: 80%;
	margin: 0 auto;
	&__subtitle {
		font-family: $handWritten;
		margin-bottom: 10px; }
	&__title {
		font-family: $titleFont;
		font-weight: 700;
		text-transform: uppercase; }
	.devider--red {
		margin: 30px auto; }
	&__descr {
		line-height: 24px;
		color: #999999;
		font-size: 14px; } }




