.about {
  @extend .section;
  &__items {
    @extend .flex_container;
    margin-top: 50px;
    justify-content: space-between; }
  &__item {
    width: 31%;
    height: 250px;
    position: relative;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: $mainColor2; }
    &_content {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: url('../img/about1.jpg');
      background-size: cover;
      background-color: transparentize(#000, .5);
      transition: all .4s; }
    &_overlay {
      height: 100%;
      text-align: center;
      background-color: transparentize($mainColor, .5);
      display: flex;
      align-items: center;
      justify-content: center;
      color: #fff;
      font-family: $titleFont;
      text-transform: uppercase;
      font-weight: 700; }
    &:hover {
      .about__item_content {
        transform: translate(-10px, -10px); } } } }
